.names-dropdown-container {
	position: absolute;
	z-index: 1060;
	padding: 1px;
	width: 100%;
}

.names-dropdown-container .name-list {
	z-index: 2;
	list-style: none;
	text-align: left;
	padding: 0;
	margin: 0 0 0 -1px;
	box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
	background-color: white;
	border: 1px solid #CCC;
	white-space: nowrap;
	max-height: 200px;
	overflow-y: scroll;
}

.names-dropdown-container * {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
}

.names-dropdown-container .name-list .name-item.highlight {
	background-color: rgba(0, 0, 0, 0.05);
}


.names-dropdown-container .name-list .name-item {
	padding: 5px 10px;
}


.names-dropdown-container .name-list .name {
	margin-right: 6px;
}

.names-dropdown-container .name-list .name {
	vertical-align: middle;
}